    msg_title: ["Consulting services for investment in SA", "Financial, tax, labor consulting, tax planning services", "Tax planning services for clients' acquisitions and investments", "Accounting services with Chinese accounting software", "Public Officer business in South Africa"],
    msg_value_add: ["Our company makes full use of its rich service experience, human resources and broad relationship in South Africa. " + "We help our clients successfully complete a large number of cross-border projects in South Africa with reasonable cost, high quality and high speed service. " + "Our customers are mainly Chinese companies in South Africa. " + "The client design industry includes construction, mining, tourism, retail, high technology, etc. " + "For the confidentiality of customer information, the following is a brief introduction of some of our customers. The customer name does not appear directly."],
    msg_value_add1: ["Customer 1. A is a domestic listed company engaged in non-metallic mineral products industry and cement manufacturing. The company invested in a cement plant in South Africa with a total investment of 220 million US dollars. Our company has undertaken all the business of A company's local finance in South Africa. Providing account, tax filing, human resource management, and audit support for one-stop service. Since all employees of our company are Chinese, there is no obstacle to language communication, and it is seamlessly connected with the financial staff of Company A. We can guarantee the first time when we provide financial taxation and labor information, and use our language advantages to explain Chinese in a confused and difficult place. At the same time, in the early stage of the establishment of Company A, our company has carried out the overall taxation of A. Planned to meet the needs of the overall tax planning of the Chinese headquarters as far as possible without violating the local tax laws of South Africa. During the project, Company A and the relevant South African Taxation Bureau showed differences in understanding of taxation as local Full-fledged financial representative, our company actively communicates with the tax bureau, timely solution The problem has been avoided. Further action by the South African Revenue Agency has been avoided. As an EPC project, it is inevitable to introduce a large number of Chinese laborers and local laborers. Among them, there are potential problems such as potential labor disputes in South Africa. Labor legal service support, as our company Another strength, we provide relevant labor law information and personal income tax information to Project A, so as to prevent it. There is no labor dispute involving South Africa. Customer 2. Company B, domestic listed company. Large-scale construction in South Africa Infrastructure projects. Our company has obtained all the financial business of Company B in South Africa. From monthly accounting, tax filing, to annual auditing. We provide comprehensive support. Due to the difference between South Africa and China's financial system, we will also cooperate with Company B. Financially conduct monthly reconciliations, minimize the difference in accounting due to the accounting system, and identify the differences. In order to meet the needs of the merger of headquarters reports."],

    msg_value_add2: ["Customer 3. C Group, a large private enterprise in Shandong. Developed the sewage project in the Eastern Cape Province of South Africa. Our company provides financial and tax services. We also designed the tax-efficient investment structure for the company's acquisition and assisted The company established a South African holding company to legally reduce the tax burden on South African income tax. We continue to provide tax planning for the post-consolidation and restructuring of the target company."],

    msg_value_add3: ["Customer 4. D company, the leader of the domestic lighting industry. Our company provides comprehensive accounting, taxation and auditing services to the South African subsidiary of Company D. At the same time, Company D conducts IPO in China, according to the domestic CSRC and the exchange. It is required that the financial statements should be comprehensively and timely merged. Because overseas companies use local accounting software for accounting, and they are accounted for in accordance with overseas local accounting standards, this leads to a complicated problem at the domestic headquarters. All employees have experience in China, so our company has also become the first company in South Africa to launch accounting services for South African business in accordance with Chinese accounting standards, using financial software familiar to domestic personnel. This greatly facilitates the consolidation of domestic headquarters reports. At the same time, the South African subsidiary does not need to deploy basic financial accounts from the domestic market."],

    msg_value_add4: ["According to South African law, if the company director is not a non-resident resident in the South, the company must appoint a Public Officer to handle the relevant tax issues in South Africa on behalf of the company. The Public Officer will act as the company's nominal director and assume the corresponding responsibilities and At the same time, the law has strict requirements for the Public Officer. It must have relevant business background, registered members of related industries, etc. Our company partner is a Public Officer registered with the South African Taxation Office and meets all the conditions. Our Public Officer customers are Major Chinese-funded enterprises."]
