    sa_info: ["南非共和国（英语：The Republic of South Africa），简称南非。地处南半球，有“彩虹之国”之美誉，位于非洲大陆的最南端，陆地面积为1219090平方公里南非位于非洲大陆最南部，其纬度自南纬22°至35°，经度从东经17°至33°。北邻纳米比亚、博茨瓦纳、津巴布韦、莫桑比克和斯威士兰，中部环抱莱索托，使其成为最大的国中国。东、南、西三面为印度洋和大西洋所环抱。其西南端的好望角航线，历来是世界上最繁忙的海上通道之一，有“西方海上生命线”之称。", "南非是非洲的第一大经济体，也是金砖五国之一，国民拥有较高的生活水平，经济相比其他非洲国家相对稳定。南非财经、法律、通讯、能源、交通业较为发达，拥有完备的硬件基础设施和股票交易市场，黄金、钻石生产量均占世界首位。深井采矿等技术居于世界领先地位。在国际事务中南非已被确定为一个中等强国，并保持显著的地区影响力。", "素有“彩虹之国”美誉的南非，自然风光绮丽，动植物种类繁多。游历一国等于环游世界，是南非的最佳写照。南非拥有富饶而多元化的文化特色、野生动物、完整的都市乡村规划、一流的道路建设、清新的空气、热情且好客的民族，加上比欧美国家更多的投资机会，吸引了成千上万的观光客和企业家前往一探究竟。", "南非的美是特别的，它，既有非洲的粗犷豪迈，同时又兼具欧洲的优雅旖旎。在原始与现代之间，与动物们来一场亲密的接触。而它五彩缤纷的色调，就如彩虹一般绚烂绮丽…… "]
